import React from "react";
import './date-filter.scss';
import {Col, Container, Dropdown, DropdownButton, Row} from "react-bootstrap";

class DateFilter extends React.Component {


    handleDropdown = (value) => {
        console.log(value);

        if (this.props.onChange) {
            this.props.onChange(value);
        }
    }

    render() {
        return (
            <div className={'date-filter-container'}>
                <Container>
                    <Row>
                        <Col md={12}>
                            <div className={'date-filter'}>
                                <p className={'label'}>Filtrar grfico</p>
                                <DropdownButton title={'Total'} id={'filter-dropdown'}>
                                    <Dropdown.Item onClick={this.handleDropdown(15)}>ltimos 15 das</Dropdown.Item>
                                    <Dropdown.Item onClick={this.handleDropdown(30)}>ltimos 30 das</Dropdown.Item>
                                    <Dropdown.Item onClick={this.handleDropdown(60)}>ltimos 60 das</Dropdown.Item>
                                    <Dropdown.Item onClick={this.handleDropdown()}>Todos los das</Dropdown.Item>
                                </DropdownButton>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default DateFilter;
